(function() {
  "use strict";
  function authenticationSuccess(data) {
    var arg;
    arg = {
      status: "auth success",
      token: data
    };
    localStorage.setItem("ot", data);
    Office.context.ui.messageParent(JSON.stringify(arg));
  }

  function authenticationFailure() {
    arg = {
      status: "failed",
      token: undefined
    };
    Office.context.ui.messageParent(JSON.stringify(arg));
  }

  function getParameterByName(name) {
    const hash = window.location.hash;
    name = name.replace(/[\[\]]/g, "\\$&");
    var regex = new RegExp("[#&]" + name + "(=([^&#]*)|&|#|$)"),
      results = regex.exec(hash);
    if (!results) return null;
    if (!results[2]) return "";
    return decodeURIComponent(results[2].replace(/\+/g, " "));
  }

  // The Office initialize function must be run each time a new page is loaded.
  Office.initialize = function() {
    $(document).ready(function() {
      const redirect_uri = window.location.origin + "/login.html";
      const token = getParameterByName("access_token");
      const hash = window.location.hash;
      if (hash.length > 0) {
        if (token) {
          authenticationSuccess(token);
        } else {
          authenticationFailure();
        }
      } else {
        const url =
          "https://ticktick.com/oauth/authorize?client_id=29xVAE6nMzik67IBhN&redirect_uri=" +
          redirect_uri +
          "&response_type=token";
        window.location.replace(url);
      }
    });
  };
})();
